import React, { CSSProperties, memo, useState } from "react";
import { useGetLocale } from "providers";

import { ReactComponent as HelpIcon } from "assets/icons/help-mui.svg";
import { Typography, MemoTable, GetCoinImg, Menu } from "components";

import { Bill } from "../types";
import Help from "./components/Help";
import css from "./style.module.scss";
interface Props {
  data: Bill[];
}

const emptyTableText: CSSProperties = {
  width: "100%",
  textAlign: "center",
  fontSize: "1.5rem",
  marginTop: 30,
};

interface Helper {
  anchor: HTMLDivElement | null;

  id: string;
  time: string;
  type: string;
}

const defaultHelperValues: Helper = {
  id: "",
  time: "",
  type: "",

  anchor: null,
};

export const Table = ({ data }: Props) => {
  const { formatMessage } = useGetLocale();
  const [helperValues, setHelperValues] = useState<Helper>({
    anchor: null,

    id: "",
    time: "",
    type: "",
  });

  const handleHideShowHelp = () => setHelperValues(defaultHelperValues);

  return (
    <div className={css.root}>
      <MemoTable
        root={{
          className: css.table,
        }}
        head={{
          values: [
            formatMessage({ id: "wallet.records.details" }),
            formatMessage({
              id: "wallet.bills.table.amount",
            }),
            formatMessage({
              id: "wallet.bills.table.balance_after",
            }),
          ],

          classes: {
            thead: css.tHead,
            th: css.tCell,
          },
        }}
        body={{
          rows: data.map(({ datetime, amount, balance_after, type, coin }) => {
            const id = `${datetime}_${balance_after}`;
            let element: HTMLDivElement | null = null;

            const handleShowHelp = () => {
              setHelperValues({
                time: datetime,
                type: type,
                id: id,
                anchor: element,
              });
            };

            return {
              values: [
                <div
                  ref={(node) => (element = node)}
                  className={css.helpWrapper}
                  onPointerMove={handleShowHelp}
                  onClick={handleShowHelp}>
                  <HelpIcon className={css.icon} />
                </div>,

                <div className={css.coinWrapper}>
                  <GetCoinImg className={css.coinIcon} coin={coin} />
                  <Typography
                    className={Number(amount) < 0 ? css.lose : css.win}
                    variant="inherit">
                    {amount}
                  </Typography>
                </div>,

                <div className={css.coinWrapper}>
                  <GetCoinImg className={css.coinIcon} coin={coin} />
                  {balance_after}
                </div>,
              ],
              key: id,
            };
          }),
          classes: {
            tbody: css.tBody,
            td: css.tCell,
          },
        }}
      />

      <Menu
        onClose={handleHideShowHelp}
        onMove={handleHideShowHelp}
        className={css.popper}
        anchor={helperValues.anchor}
        isOpen={!!helperValues.anchor}>
        <>
          <Help
            show={!!helperValues.anchor}
            time={helperValues.time}
            type={helperValues.type}
          />
        </>
      </Menu>

      {data.length === 0 && (
        <div style={emptyTableText}>
          {formatMessage({ id: "wallet.emptyTable" })}
        </div>
      )}
    </div>
  );
};

export default Table;
