import React, { FC } from "react";
import { useGetLocale } from "providers";

import css from "./style.module.scss";
import { Transition } from "components";

interface BillTableHelpProps {
  show: boolean;
  time: string;
  type: string;
}

const Help: FC<BillTableHelpProps> = ({ show, time, type }) => {
  const intl = useGetLocale();
  return (
    <Transition className={css.root} animation="fade" in={show}>
      <div className={css.box}>
        <div className={css.wrapper}>
          <h5 className={css.title}>
            {intl.formatMessage({
              id: "wallet.bills.table.datetime",
            })}
            :
          </h5>
          <p className={css.text}>{time}</p>
        </div>
        <div className={css.wrapper}>
          <h5 className={css.title}>
            {intl.formatMessage({
              id: "wallet.bills.table.type",
            })}
            :
          </h5>
          <p className={css.text}>{type}</p>
        </div>
      </div>
    </Transition>
  );
};

export default Help;
