import React, { FunctionComponent, useEffect, useState } from "react";

import { useFetch, useLocalStorage } from "hooks";
import Pagination from "pages/ModalPages/pages/GameHistory/components/Pagination";

import { Table } from "./Table";
import { Bill, BillsAnswer } from "./types";
import css from "./style.module.scss";

interface Props {
  currency: string;
}

const Bills: FunctionComponent<Props> = ({ currency }) => {
  const [token] = useLocalStorage("token");

  const [page, setPage] = useState(1);
  const [content, setContent] = useState<Bill[]>([]);

  const [billsFetch, { data: billsData, loading }] = useFetch<BillsAnswer>(
    "history/transactions",
  );

  const fetchDataFromPage = (page: number) => {
    billsFetch({
      variables: {
        token,
        page,
      },
    });
  };

  useEffect(() => {
    if (token) {
      fetchDataFromPage(page);
    } else if (page !== 1) {
      setPage(1);
    }
  }, [token, page]);

  useEffect(() => {
    if (billsData) {
      if (billsData.transactions.length > 0) {
        setContent(billsData.transactions);
      } else if (page > 0) {
        setPage(page - 1);
      }
    }
  }, [billsData]);

  const setNextPage = () => {
    setPage(page + 1);
  };
  const setPrevPage = () => {
    setPage(page - 1);
  };

  return (
    <div className={css.root}>
      <div className={css.paginationBlock}>
        <Pagination
          disabledBefore={loading || page <= 1}
          disabledAfter={loading || content.length < 15}
          page={page}
          setNextPage={setNextPage}
          setPrevPage={setPrevPage}
          className={css.pagination}
        />
      </div>
      <div className={css.tableWrapper}>
        <Table data={content} />
      </div>
    </div>
  );
};

export default Bills;
